export default function bubbleSort(array) {
  let size = array.length;
  let result = [];

  for (let i = 0; i < size - 1; i++)
    for (let j = 0; j < size - i - 1; j++) {
      result.push("h");
      result.push(j);
      result.push(j + 1);

      if (array[j] > array[j + 1]) {
        [array[j], array[j + 1]] = [array[j + 1], array[j]];

        result.push("s");
        result.push(j);
        result.push(j + 1);
      }
    }
  return result;
}
