export default function combSort(array) {
  let size = array.length;
  let result = [];
  let factor = 1.247;
  let step = size - 1;

  while (step >= 1) {
    for (let i = 0; i + step < size; ++i) {
      result.push("h");
      result.push(i);
      result.push(parseInt(i + step, 10));

      if (array[i] > array[parseInt(i + step, 10)]) {
        [array[i], array[parseInt(i + step, 10)]] = [
          array[parseInt(i + step, 10)],
          array[i],
        ];

        result.push("s");
        result.push(i);
        result.push(parseInt(i + step, 10));
      }
    }
    step = step / factor;
  }

  return result;
}
