export default function insertionSort(array) {
  let size = array.length;
  let result = [];

  for (let i = 1; i < size; ++i) {
    let x = array[i];
    result.push("h");
    result.push(i);
    let j = i;

    while (j > 0 && array[j - 1] > x) {
      array[j] = array[j - 1];

      result.push("h");
      result.push(j - 1);
      result.push("c");
      result.push(array[j - 1]);
      result.push(j);

      --j;
    }

    array[j] = x;
    result.push("c");
    result.push(x);
    result.push(j);
  }
  return result;
}
