export default function shakerSort(array) {
  let size = array.length;
  let result = [];

  let left = 0;
  let right = size - 1;

  while (left <= right) {
    for (let i = right; i > left; --i) {
      result.push("h");
      result.push(i - 1);
      result.push(i);

      if (array[i - 1] > array[i]) {
        [array[i - 1], array[i]] = [array[i], array[i - 1]];

        result.push("s");
        result.push(i - 1);
        result.push(i);
      }
    }
    ++left;
    for (let i = left; i < right; ++i) {
      result.push("h");
      result.push(i);
      result.push(i + 1);

      if (array[i] > array[i + 1]) {
        [array[i], array[i + 1]] = [array[i + 1], array[i]];

        result.push("s");
        result.push(i);
        result.push(i + 1);
      }
    }
    --right;
  }
  return result;
}
