import React from "react";
import "./Visualizer.css";
import bubbleSort from "./algorithms/bubbleSort.js";
import shakerSort from "./algorithms/shakerSort.js";
import insertionSort from "./algorithms/insertionSort.js";
import combSort from "./algorithms/combSort.js";

const min = 0;
const max = 500;
let is_working = false;
let is_animating = false;
let is_cancelling = false;
const defaultColor = "skyblue";

export default class Visualizer extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      array: [],
    };
  }

  componentDidMount() {
    this.generateArray();
  }

  generateArray() {
    let size = document.getElementById("size").value;
    const array = [];
    for (let i = 0; i < size; i++) {
      array.push(randomInt(min, max));
    }

    this.setState({ array });
  }

  async reset() {
    is_cancelling = true;

    while (is_animating) {
      await new Promise((res) => setTimeout(res, 10));
    }
    changeToColor(defaultColor);
    this.generateArray();

    is_cancelling = false;
    is_working = false;
  }

  render() {
    const array = this.state.array;

    let size = array.length;
    let margins = 2 * size;
    let width = 100 / (size + margins / 4);

    return (
      <div className="App">
        <div className="bar-container-container">
          <div className="bar-container">
            {array.map((value, index) => (
              <div
                className="bar"
                key={index}
                style={{
                  height: `${((value - min + 1) / (max - min + 1)) * 100}%`,
                  width: `${width}%`,
                  marginLeft: `${width / 4}%`,
                  marginRight: `${width / 4}%`,
                }}
              ></div>
            ))}
          </div>
        </div>

        <div className="menu">
          <select id="alg" name="alghorithms">
            <option value="bubbleSort">Пузырьковая сортировка</option>
            <option value="insertionSort">Сортировка вставками</option>
            <option value="shakerSort">Сортировка перемешиванием</option>
            <option value="combSort">Сортировка расчёской</option>
            <option value="quickSort">Быстрая сортировка</option>
            <option value="mergeSort">Соритровка слиянием</option>
          </select>
          <br></br>
          <button
            id="startButton"
            className="start-button"
            onClick={() => start(this.state.array, "bubbleSort")}
          >
            Пуск
          </button>
          <button
            id="reset"
            className="reset-button"
            onClick={() => this.reset()}
          >
            Сброс
          </button>
          <div className="slidercontainer">
            <input
              className="slider"
              type="range"
              min="10"
              max="300"
              defaultValue="50"
              step="1"
              id="size"
            ></input>
          </div>

          <div className="slidercontainer">
            <input
              className="slider"
              type="range"
              min="0"
              max="499"
              defaultValue="300"
              step="1"
              id="speed"
            ></input>
          </div>
        </div>
      </div>
    );
  }
}

function randomInt(min, max) {
  return Math.floor(Math.random() * (max - min + 1) + min);
}

async function start(array) {
  let arrayCopy = array.slice(0);
  if (!is_working && !is_cancelling) {
    is_working = true;
    let alg = document.getElementById("alg").value;
    if (alg === "bubbleSort") animate(bubbleSort(arrayCopy), array);
    if (alg === "shakerSort") animate(shakerSort(arrayCopy), array);
    if (alg === "insertionSort") animate(insertionSort(arrayCopy), array);
    if (alg === "combSort") animate(combSort(arrayCopy), array);
  } else if (is_working && !is_cancelling) {
    is_cancelling = true;

    while (is_animating) {
      await new Promise((res) => setTimeout(res, 10));
    }
    changeToColor(defaultColor);

    is_cancelling = false;
    is_working = false;
  }
}

function changeToColor(color) {
  const bars = document.getElementsByClassName("bar");
  if (bars != null) {
    for (let i = 0; i < bars.length; ++i) {
      bars[i].style.backgroundColor = color;
    }
  }
}

async function animate(format, array) {
  is_animating = true;
  const bars = document.getElementsByClassName("bar");

  let i = 0;
  const elem = document.getElementById("speed");
  let changed = [];

  let iteration = 1;
  while (i < format.length) {
    if (is_cancelling) {
      is_animating = false;
      return;
    }
    let speed = elem.value;
    let time = Math.max(400 - speed, 10);

    let k;
    if (speed <= 420) k = 1;
    else if (speed > 495) k = (speed - 490) * 3 + 10;
    else if (speed > 490) k = 16;
    else if (speed > 480) k = 8;
    else if (speed > 460) k = 5;
    else if (speed > 440) k = 3;
    else k = 2;

    for (let j = 0; j < changed.length; j++) {
      changed[j].backgroundColor = defaultColor;
    }

    changed = [];

    if (format[i] === "h") {
      i++;
      while (!isNaN(format[i])) {
        let a = bars[format[i]].style;
        a.backgroundColor = "red";

        changed.push(a);
        i++;
      }
    } else if (format[i] === "s") {
      i++;
      let index1 = format[i];
      let index2 = format[i + 1];
      [array[index1], array[index2]] = [array[index2], array[index1]];

      let a1 = bars[index1].style;
      let a2 = bars[index2].style;

      a1.backgroundColor = "green";
      a2.backgroundColor = "green";
      //if (speed < 360) await new Promise((res) => setTimeout(res, time / 2));

      a1.height = `${((array[index1] - min + 1) / (max - min + 1)) * 100}% `;
      a2.height = `${((array[index2] - min + 1) / (max - min + 1)) * 100}% `;

      changed.push(a1);
      changed.push(a2);
      i += 2;
    } else if (format[i] === "c") {
      i++;

      let index = format[i + 1];
      let a1 = bars[index].style;
      array[index] = format[i];

      a1.backgroundColor = "green";
      a1.height = `${((array[index] - min + 1) / (max - min + 1)) * 100}% `;

      changed.push(a1);
      i += 2;
    }
    if (iteration % k === 0) {
      await new Promise((res) => setTimeout(res, time));
    }
    iteration++;
  }
  for (let j = 0; j < changed.length; j++) {
    changed[j].backgroundColor = defaultColor;
  }
  is_animating = false;
  is_working = false;
}
